.products {
  position: relative;
  &__bg {
    position: absolute;
    inset: 0;
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
    &:before {
      content: "";
      position: absolute;
      inset: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.2);
      z-index: 2;
    }
  }
  &-info {
    position: relative;
    z-index: 3;
    padding: 50px 0 144px;
    @include screen(laptop) {
      padding: 120px 0 73px;
    }
    &__wrapper {
      h2 {
        position: relative;
        font-family: $DXRigrafEx;
        text-transform: uppercase;
        z-index: 2;
        position: relative;
        font-size: 32px;
        line-height: 36px;
        @include screen(laptop) {
          font-size: 42px;
          line-height: 48px;
        }
        .line {
          &:before {
            content: "";
            position: absolute;
            bottom: -4px;
            z-index: -1;
            left: 0;
            width: 100%;
            height: 20px;
            background-image: url(data:image/svg+xml;base64,);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 100%;
          }
          &-mob {
            &:before {
              content: "";
              position: absolute;
              bottom: -12px;
              z-index: -1;
              left: 0;
              width: 100%;
              height: 40px;
              background-image: url(data:image/svg+xml;base64,);
              background-repeat: no-repeat;
              background-position: center;
              background-size: 100%;
              @include screen(mobile) {
                bottom: 0;
                display: none;
              }
            }
          }
        }
        br {
          @include screen(laptop) {
            display: none;
          }
        }
        span {
          position: relative;
          z-index: 2;
        }
      }
      ol {
        max-width: 448px;
        margin-top: 20px;
        counter-reset: section;
        display: flex;
        flex-direction: column;
        row-gap: 20px;
        @include screen(laptop) {
          row-gap: 30px;
          margin-top: 48px;
        }
        li {
          counter-increment: section;
          font-size: 14px;
          line-height: 19px;
          font-family: $Inter;
          letter-spacing: 0.2px;
          &:before {
            content: counter(section) ". ";
          }
          @include screen(laptop) {
            font-size: 24px;
            line-height: 30px;
          }
        }
      }
    }
  }
  &-reviews {
    position: relative;
    z-index: 3;
    background-color: $purple;
    padding: 28px 0 44px;
    @include screen(laptop) {
      background-color: $purple-dark;
      padding: 28px 0 179px;
    }
    .swiper {
      @include screen(laptop) {
        max-width: 469px;
        margin: 0 0 0 32px;
      }
      &-slide {
        @include screen(laptop) {
          min-height: 227px;
        }
      }
    }
    &__link {
      margin-top: 20px;
      display: inline-block;
      position: relative;
      transition: color $transition-default;
      &:hover {
        color: $red;
        &:before {
          opacity: 1;
        }
      }
      &:before {
        content: "";
        position: absolute;
        bottom: -5px;
        left: 0;
        width: 100%;
        height: 5px;
        background-image: url(data:image/svg+xml;base64,);
        background-repeat: no-repeat;
        background-position: center;
        background-size: 100%;
        opacity: 0;
        transition: opacity $transition-default;
      }
      @include screen(laptop) {
        margin-left: 32px;
      }
    }
    &__rating {
      display: flex;
      column-gap: 6px;
      align-items: center;
    }
    &__title {
      margin-top: 15px;
      display: flex;
      align-items: center;
      column-gap: 4px;
      @include screen(laptop) {
        margin-top: 20px;
      }
      span {
        font-size: 20px;
        font-family: $DXRigrafEx;
        line-height: 22px;
      }
    }
    &__text {
      margin-top: 12px;
      font-family: $Inter;
      line-height: 22px;
      display: flex;
      flex-direction: column;
      row-gap: 16px;
      @include screen(laptop) {
        max-width: 469px;
      }
    }
  }
  &-items {
    background-color: $purple-dark;
    position: relative;
    z-index: 5;
    padding-bottom: 53px;
    @include screen(laptop) {
      background-color: transparent;
      position: absolute;
      width: 57%;
      overflow: visible;
      padding-bottom: 0;
      top: 50%;
      right: 50px;
      transform: translateY(-50%);
    }
    &__substrate {
      position: absolute;
      bottom: 160px;
      width: 761px;
      transform: translateX(-50%);
      left: 50%;
      @include screen(laptop) {
        bottom: 120px;
      }
      @include screen(laptop) {
        display: none;
      }
    }
    &__buttons {
      display: flex;
      margin-top: 30px;
      flex-direction: column;
      row-gap: 20px;
      @include screen(laptop) {
        column-gap: 25px;
        flex-direction: row;
      }
    }
    .swiper {
      transform: translateY(-102px);
      user-select: none;
      margin-bottom: -102px;
      &-wrapper {
        align-items: stretch;
      }
      @include screen(laptop) {
        margin-bottom: 0;
        overflow: visible;
        transform: translateY(0);
      }
      &-slide {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        height: auto;
        img {
          max-width: 90%;
          min-height: 380px;
          object-fit: contain;
        }
      }
      &-button {
        &-prev,
        &-next {
          width: 30px;
          height: 30px;
          @include screen(laptop) {
            width: auto;
            height: auto;
          }
          &:before,
          &:after {
            display: none;
          }
        }
        &-prev {
          @include screen(laptop) {
            left: -50px;
          }
        }
        &-next {
          @include screen(laptop) {
            right: -50px;
          }
        }
      }
    }
  }
}